export default {
	data() {
		return {
			news: []
		}
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e) {
			var newsid = e.currentTarget.dataset.newsid;
			console.log(newsid);
			uni.navigateTo({
				url: '../info/info?newsid=' + newsid
			});
		}
	}
}
